.service-card-img {
	position: relative;
	width: 100%;
	padding-top: 43%;

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: top left;
	}

	&_cover {
		margin-top: -15px;
		margin-right: -30px;
		margin-left: -30px;
		padding-top: calc(45% + 15px);

		.service-card-img__img {
			object-fit: cover;
			object-position: center center;
		}
	}

	&_size_m {
		padding-top: 66%;

		&.service-card-img_cover {
			padding-top: calc(66% + 15px);
		}
	}

	&_size_l {
		padding-top: calc(100% - 15px);

		&.service-card-img_cover {
			padding-top: calc(100%);
		}
	}

	&_size_xl {
		padding-top: 124%;

		&.service-card-img_cover {
			padding-top: calc(124% + 15px);
		}
	}
}
